import React, { Component } from 'react';
import { Helmet } from "react-helmet";
import SEO from "../components/seo"
import BlogList from '../components/blogList'
import importBlogs from '../utils/importBlogs';
import ChartList from '../components/chartList'
import importCharts from '../utils/importCharts';

class Home extends Component {
    constructor(props) {
        super(props);
        this.state = {
            blogs: [],
            charts: []
        }
    }

    componentDidMount() {
        if (typeof window !== `undefined`) {
            var image = document.getElementById('vrc__homeImg');
            new window.simpleParallax(image, {
                scale: 1.8,
                delay: .6,
                transition: 'cubic-bezier(0,0,0,1)'
            })
        }
        importBlogs((blog) => {
            this.setState((state) => { return { blogs: state.blogs.concat([blog]) } })
        });
        importCharts((chart) => {
            this.setState((state) => { return { charts: state.charts.concat([chart]) } })
        });
    }

    render() {
        const { blogs, charts } = this.state;
        return (
            <>
                <SEO title="Home" />
                <div className="vrc__homeImgContainer">
                    <img src="../../home.jpg" id="vrc__homeImg" />
                </div>
                <div className="container">
                    <br />
                    <h1>Welcome to VrC site.</h1>

                    <br />
                    <p>How you'll enjoy :)</p>
                    <hr />
                    <h2>Blogs</h2>
                    <BlogList blogs={blogs} />
                    <hr />
                    <h2>Charts</h2>
                    <ChartList charts={charts} />
                </div>
            </>
        )
    }
}

export default Home;